.header {
  padding-top: 30px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    background: #F2F2F2;
    border-radius: 20px;
    padding: 15px 30px;
  }
  &__logo {
    font-family: 'Prompt';
    font-size: 35px;
    line-height: 150%;
    font-weight: 600;
    letter-spacing: -0.7px;
  }
  &__btn {
    height: 60px;
    border-radius: 11px;
    padding: 5px 36px;
    span {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 32px;
    li a {
      font-size: 20px;
      line-height: 150%;
      font-weight: 500;
      &:hover {
        color: #4C6FED;
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 16px;
  }
}
.burger {
  position: relative;
  z-index: 5;
  width: 36px;
  height: 26px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background: #4C6FED;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 11px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 11px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__inner {
      padding: 15px;
    }
    &__logo {
      font-size: 30px;
      letter-spacing: -0.6px;
    }
    &__list {
      position: fixed;
      z-index: 4;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      background: #222;
      padding: 150px 15px 50px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      li a {
        color: #fff;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      font-size: 24px;
      letter-spacing: -0.48px;
    }
    &__btn {
      height: 50px;
      padding: 5px 20px;
      span {
        font-size: 14px;
      }
      i {
        max-width: 16px !important;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__btn {
      width: 40px;
      height: 40px;
      padding: 5px;
      span {
        display: none;
      }
    }
  }
}