.main {
  position: relative;
  padding: 50px 0 0 0;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -29px;
    transform: translate(-50%, 0);
    width: 63px;
    height: 63px;
    background: #fff;
    border-radius: 50%;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50px;
    padding: 18px 30px 100px;
    overflow: hidden;
    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      width: 1064px;
      height: 574px;
      background: #4d7bff;
      -webkit-filter: blur(131.2px);
      filter: blur(131.2px);
    }
  }
  &__wrap {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title {
    max-width: 982px;
    font-family: 'Prompt';
    font-size: 62px;
    line-height: 150%;
    font-weight: 600;
    letter-spacing: -1.24px;
    color: #fff;
    span {
      background-image: url("../img/line.svg");
      background-repeat: no-repeat;
      background-position: center top 10px;
      background-size: cover;
    }
  }
  &__subtitle {
    font-size: 18px;
    line-height: 116%;
    letter-spacing: -0.36px;
    color: #fff;
    margin-top: 20px;
  }
  &__btn {
    height: 66px;
    border-radius: 11px;
    padding: 5px 40px;
    margin-top: 40px;
    span {
      font-size: 18px;
      letter-spacing: -0.36px;
    }
  }
  &__btn2 {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: -20px;
    transform: translate(-50%, 0);
    width: 43px;
    height: 43px;
    background: #F2F2F2;
    border-radius: 50%;
  }
}
.finance {
  padding: 100px 0 40px;
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 40px;
  }
}
.finance-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  &__desc {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 15px;
  }
  &__icon {
    width: 38px;
    margin-bottom: 15px;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 140%;
    font-weight: 500;
    letter-spacing: -0.3px;
    color: #737373;
  }
  &__img {
    img {
      width: 100%;
      border-radius: 50px;
    }
  }
}
.connection {
  position: relative;
  padding: 40px 0;
  &__inner {
    position: relative;
  }
  &__title {
    max-width: 890px;
    text-align: center;
    margin: 0 auto;
    span {
      color: #4C6FED;
    }
  }
  &__img {
    position: absolute;
    top: 140px;
    right: 100px;
    width: 166px;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    right: 190px;
    bottom: 0;
    width: 382px;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    top: 180px;
    left: 100px;
    width: 118px;
    img {
      width: 100%;
    }
  }
  &__img4 {
    position: absolute;
    left: 190px;
    bottom: 0;
    width: 382px;
    img {
      width: 100%;
    }
  }
}
.connection-form {
  display: grid;
  grid-template-columns: 100%;
  gap: 15px;
  margin-top: 20px;
  &__wrap {
    position: relative;
    z-index: 2;
    max-width: 536px;
    background: linear-gradient(180.00deg, rgb(80, 114, 238),rgb(58, 96, 234) 100%);
    border-radius: 50px;
    padding: 43px 73px;
    margin: 30px auto 0;
  }
  &__title {
    text-align: center;
    font-family: 'Prompt';
    font-size: 24px;
    line-height: 150%;
    letter-spacing: -0.48px;
    color: #E4E9FC;
  }
  &__group {
    input {
      width: 100%;
      height: 60px;
      background: #fff;
      border: 1px solid transparent;
      border-radius: 15px;
      padding: 0 30px;
      font-size: 16px;
      line-height: 112%;
      letter-spacing: -0.32px;
      color: #222;
      &::placeholder {
        color: #9E9E9E;
      }
    }
    .text-danger {
      display: block;
      font-size: 16px;
      line-height: 112%;
      letter-spacing: -0.32px;
      color: #ff0000;
      margin-top: 5px;
    }
  }
  &__btn {
    width: 100%;
    height: 66px;
    span {
      font-size: 18px;
    }
  }
}
.advantages {
  padding: 40px 0;
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 255px / 4));
    gap: 85px;
    margin-top: 20px;
  }
}
.advantage-card {
  position: relative;
  &:not(:last-child) {
    &:after {
      content: '';
      position: absolute;
      right: -42px;
      bottom: 0;
      width: 1px;
      height: 84px;
      background: #ECECEC;
      pointer-events: none;
    }
  }
  &__img {
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc {
    text-align: center;
    margin-top: 12px;
  }
  &__title {
    font-family: 'Prompt';
    font-size: 24px;
    line-height: 150%;
    font-weight: 600;
    letter-spacing: -0.48px;
    color: #4E70EE;
  }
  &__text {
    font-size: 16px;
    line-height: 140%;
    font-weight: 500;
    letter-spacing: -0.32px;
    color: #9E9E9E;
    margin-top: 5px;
  }
}
.questions {
  padding: 40px 0;
  &__title {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 30px;
  }
  &__item {
    background: #F2F2F2;
    border-radius: 20px;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 16px;
      padding: 25px 25px 25px 40px;
      cursor: pointer;
    }
    .item-show--active {
      .questions__item-btn {
        span:nth-child(1) {
          display: none;
        }
        span:nth-child(2) {
          display: block;
        }
      }
    }
    &-title {
      font-size: 20px;
      line-height: 110%;
      font-weight: 700;
      letter-spacing: -0.4px;
    }
    &-btn {
      width: 42px;
      height: 42px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #466AEC;
      border-radius: 50%;
      span {
        font-size: 30px;
        line-height: 105%;
        color: #fff;
      }
      span:nth-child(2) {
        display: none;
      }
    }
    &-hidden {
      display: none;
      padding: 0 25px 25px 40px;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      p {
        font-size: 20px;
        line-height: 110%;
        letter-spacing: -0.4px;
        color: #9E9E9E;
      }
    }
  }
}
.reviews {
  padding: 40px 0;
  &__title {
    text-align: center;
  }
  &__slider {
    position: relative;
    max-width: 675px;
    margin: 35px auto 0;
  }
  .swiper {
    overflow: unset;
  }
}
.review-card {
  background: #fff;
  box-shadow: 2px 8px 16px 0px rgba(194, 198, 208, 0.06);
  border: 2px solid #F2F2F2;
  border-radius: 50px;
  padding: 62px 78px;
  &__user {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    &-img {
      width: 50px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-name {
      font-size: 14px;
      line-height: 140%;
      font-weight: 600;
    }
  }
  &__text {
    font-size: 20px;
    line-height: 140%;
    font-weight: 500;
    margin-top: 20px;
  }
}
.connection2 {
  padding: 40px 0 80px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 90px;
  }
  .connection-form__wrap {
    width: 50%;
    margin-top: 0;
  }
  .connection__desc {
    width: 50%;
  }
  .connection__title {
    text-align: start;
  }
  .connection__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
    margin-top: 30px;
    li {
      position: relative;
      padding-left: 35px;
      font-size: 18px;
      line-height: 140%;
      font-weight: 500;
      &:after {
        content: '';
        position: absolute;
        top: 5px;
        left: 0;
        width: 15px;
        height: 15px;
        background-image: url("../img/plus.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
  }
}
.policy {
  padding: 80px 0;
  &__inner {
    h2 {
      margin-bottom: 12px;
    }
    p {
      font-size: 18px;
      line-height: 188%;
    }
    hr {
      width: 100%;
      height: 30px;
      padding: 0;
      margin: 0;
      opacity: 0;
      pointer-events: none;
    }
    a {
      display: inline-flex;
      align-items: center;
      gap: 10px;
      color: #4C6FED;
      margin-left: 5px;
      i {
        width: 20px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 24px;
        line-height: 140%;
        font-weight: 500;
      }
    }
  }
}

@media screen and (max-width: 1700px) {
  .connection {
    &__img2 {
      right: 15px;
    }
    &__img4 {
      left: 15px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .connection {
    &__img2 {
      width: 250px;
    }
    &__img4 {
      width: 250px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .connection {
    &__img {
      right: -30px;
    }
    &__img3 {
      left: -30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding-top: 30px;
    &__title {
      font-size: 40px;
      letter-spacing: -0.78px;
      span {
        background-image: unset;
        border-bottom: 5px dashed #222;
      }
    }
  }
  .finance {
    padding: 60px 0 30px;
    &__cards {
      margin-top: 20px;
    }
  }
  .finance-card {
    &__img {
      img {
        border-radius: 30px;
      }
    }
  }
  .connection {
    padding: 30px 0;
  }
  .connection-form {
    &__wrap {
      max-width: 450px;
      border-radius: 30px;
      padding: 40px 20px;
      margin-top: 20px;
    }
  }
  .advantages {
    padding: 30px 0;
    &__cards {
      grid-template-columns: repeat(4, calc(25% - 60px / 4));
      gap: 20px;
    }
  }
  .advantage-card {
    &:not(:last-child) {
      &:after {
        right: -9px;
      }
    }
  }
  .questions {
    padding: 30px 0 60px;
    &__items {
      margin-top: 30px;
    }
    &__item {
      &-show {
        padding: 25px;
      }
      &-title {
        font-size: 18px;
        letter-spacing: -0.36px;
      }
      &-hidden {
        padding: 0 25px 25px 25px;
      }
      &-text {
        p {
          font-size: 18px;
          letter-spacing: -0.36px;
        }
      }
    }
  }
  .reviews {
    padding: 30px 0;
    &__slider {
      max-width: 400px;
      margin-top: 20px;
    }
  }
  .review-card {
    border-radius: 30px;
    padding: 24px;
    &__text {
      font-size: 16px;
      margin-top: 16px;
    }
  }
  .connection2 {
    padding: 30px 0 60px;
    &__inner {
      gap: 30px;
    }
    .connection__list {
      gap: 20px;
      margin-top: 20px;
      li {
        font-size: 16px;
        &:after {
          top: 2px;
        }
      }
    }
  }
  .policy {
    padding: 60px 0;
    &__inner {
      p {
        font-size: 16px;
      }
      hr {
        height: 20px;
      }
      a {
        span {
          font-size: 18px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .finance {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  .connection {
    &__img {
      right: 0;
      width: 100px;
    }
    &__img2,
    &__img4 {
      display: none;
    }
    &__img3 {
      left: 0;
      width: 70px;
    }
  }
  .advantages {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  .advantage-card {
    &:after {
      display: none !important;
    }
  }
  .connection2 {
    &__inner {
      flex-direction: column;
    }
    .connection-form__wrap {
      width: 100%;
      margin: 0 auto;
    }
    .connection__desc {
      width: 100%;
      order: -1;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__inner {
      border-radius: 30px;
      padding: 18px 15px 80px;
    }
    &__title {
      font-size: 30px;
      letter-spacing: -0.6px;
    }
    &__subtitle {
      font-size: 16px;
      margin-top: 15px;
    }
    &__btn {
      height: 56px;
      padding: 5px 40px;
      span {
        font-size: 16px;
      }
    }
  }
  .finance {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 12px / 2));
      gap: 12px;
    }
  }
  .finance-card {
    &__text {
      font-size: 14px;
      letter-spacing: -0.28px;
    }
  }
  .connection-form {
    &__group {
      input {
        height: 50px;
      }
    }
    &__btn {
      height: 56px;
    }
  }
  .advantages {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 12px / 2));
      gap: 12px;
    }
  }
  .advantage-card {
    &__img {
      max-width: 100px;
      max-height: 100px;
      margin: 0 auto;
    }
    &__title {
      font-size: 18px;
      letter-spacing: -0.36px;
    }
    &__text {
      font-size: 14px;
      letter-spacing: -0.28px;
    }
  }
  .questions {
    &__items {
      gap: 12px;
    }
    &__item {
      &-show {
        padding: 16px;
      }
      &-title {
        font-size: 16px;
      }
      &-hidden {
        padding: 0 16px 16px 16px;
      }
      &-text {
        p {
          font-size: 16px;
        }
      }
    }
  }
}